.drawer-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
  overflow: hidden;
}

.drawer {
  flex: 1;
  overflow: auto;
}

.drawer-app-content {
  flex: 3;
  overflow: auto;
}

.course-list-item :hover {
  cursor: pointer;
}

.fab {
  position: fixed;
  right: 4em;
  bottom: 4em;
  border-radius: 3em;
  width: 6em !important;
  height: 6em !important;
}

.schedule-select {
  width: 100%;
}

.days {
  display: flex;
  position: relative;
  margin-left: 50;
  height: 100%;
  padding: 36px 0 0 40px;
  box-sizing: border-box;
}

.day {
  position: relative;
  flex-grow: 1;
  flex-basis: 0;
  margin: 0px 10px;
}

.week-heading {
  text-align: center;
  display: block;
  margin: -40px 10px;
  color: grey;
  font-weight: bold;
}

.week-wrap {
  overflow: auto;
  position: absolute;
  padding-bottom: 5px;
  width: 100%;
  height: 100%;
}

.week-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.week-grid {
  position: absolute;
  height: 100%;
  width: 100%;
  border-spacing: 0;
  box-sizing: border-box;
  padding: 0 0 10px 0;
}

.week {
  position: absolute;
  top: 60;
  width: 100%;
  height: 100%;
}